import { Storage } from 'aws-amplify'

function App() {
  async function onChange(e) {
    const file = e.target.files[0];
    const result = await Storage.put(file.name, file)
    console.log({ result })
  }
  return (
    <div className="App">
      <h1>Image Upload</h1>
      <input
        type="file"
        accept="image/*"
        onChange={onChange}
      />
    </div>
  );
}

export default App;